* {
  margin: 0;
  padding: 0;
}

body * {
  font-family: "Staatliches", cursive;
}

.App::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  min-width: 50%;
  min-height: 50%;
  background: rgb(25 34 41);
  opacity: 0.5;
  backdrop-filter: blur(5px);
}
.App {
  background: url("./images/bg.jpg") no-repeat center center fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  min-width: 50%;
  min-height: 50%;
}
.card {
  position: absolute !important;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 550px;
  min-height: 300px;
  background: #fff;
  box-shadow: 0 20px 50px rgba(0, 0, 0, 0.1);
  border-radius: 0px;
  transition: 0.5s;
}
.card:hover {
  box-shadow: 0 30px 70px rgba(0, 0, 0, 0.2);
}
.card .box {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  text-align: center;
  padding: 20px;
  box-sizing: border-box;
  width: 100%;
}
.card .box .img {
  width: 120px;
  height: 120px;
  margin: 0 auto;
  border-radius: 50%;
  overflow: hidden;
}
.card .box .img img {
  width: 100%;
  height: 100%;
}
.card .box h2 {
  font-size: 20px;
  color: #262626;
  margin: 20px auto;
}
.card .box h2 span {
  font-size: 14px;
  background: #e91e63;
  color: #fff;
  display: inline-block;
  padding: 4px 10px;
  border-radius: 15px;
}
.card .box p {
  color: #262626;
}
.card .box span {
  display: inline-flex;
}
.card .box ul {
  margin: 0;
  padding: 0;
}
.card .box ul li {
  list-style: none;
  float: left;
}
.card .box ul li a {
  display: block;
  color: #aaa;
  margin: 0 10px;
  font-size: 20px;
  transition: 0.5s;
  text-align: center;
}
.card .box ul li:hover a {
  color: #e91e63;
  transform: rotateY(360deg);
}

.btn-primary-btn {
  text-transform: uppercase;
  -webkit-font-smoothing: subpixel-antialiased;
  background-color: rgb(233 30 99);
  /* top: -4px; */
  /* left: 0px; */
  border-radius: 4px;
  color: #ffffff;
  cursor: pointer;
  margin-top: 25px;
  width: 200px;
  transition: transform 0s ease 0s;
  font-weight: 500;
  outline: none;
  outline-style: none !important;
  border: none;
  font-size: 18px;
  height: 40px;
}
.bold {
  font-weight: 400;
  color: #262626;
  font-size: 18px;
  line-height: 27px;
  padding: 30px;
}

@media (max-width: 600px) {
  .card {
    width: 90%;
  }
}
